"use strict";

import $ from "jquery";

document.addEventListener("DOMContentLoaded", function(event) {
    const Numbers = {
        numbersSection: document.querySelector('.numbers'),
        windowHeight: document.documentElement.clientHeight,
        animate: true,
        
        isVisible: function(elem) {
            let coords = elem.getBoundingClientRect();
            let topVisible = coords.top >= 0 && coords.top < this.windowHeight;
            let bottomVisible = coords.bottom < this.windowHeight && coords.bottom >= 0;
    
            return topVisible || bottomVisible;
        },
        scrollIsVisible: function() {
            let self = this;

            function commaSeparateNumber(val) {
                while (/(\d+)(\d{3})/.test(val.toString())) {
                    val = val.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,");
                }
                return val;
            }
    
            document.addEventListener('scroll', function() {
                if (self.isVisible(self.numbersSection) && self.animate) {
                    $('[data-number]').each(function () {
                        $(this).prop('Counter',0).animate({
                            Counter: $(this).text()
                        }, {
                            duration: 2000,
                            easing: 'swing',
                            step: function (now) {
                                // $(this).text(Math.ceil(now));
                                $(this).text(commaSeparateNumber(Math.round(now)));
                            }
                        });
                    });
    
                    self.animate = false;
                }
            });
        },
        init: function() {
            this.scrollIsVisible();
        }
    }
    
    Numbers.init();
});
